.row-container{
  margin-left: 20px;
  color: white;

}


.row-cards{
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}
.row-cards::-webkit-scrollbar{
  display: none;
}

.movie-card{
  object-fit: contain;
 width: 100%;;
 max-height: 180px;
 margin-bottom: 10px;
 transition: transform 450ms;
}
.movie-card:hover{
  transform: scale(2.08);
   width: 100%;
   max-height: 180px;
   margin-bottom: 10px;
  }
.movie-card-large{
  object-fit: contain;
 width: 100%;
 max-height: 250px;
 margin-bottom: 10px;
 transition: transform 450ms;
}


img.movie-card-large:hover{
  transform: scale(1.08);
  width: 100%;
  max-height: 250px;
  margin-bottom: 10px;
}